export function getEtherscanLink(
  chainId: ChainId,
  data: string,
  type: 'transaction' | 'token' | 'address' | 'block',
): string {
  const blockExplorerBaseUrl = `https://${BLOCK_EXPLORER_URL[chainId] || BLOCK_EXPLORER_URL[1]}`;

  switch (type) {
    case 'transaction': {
      return `${blockExplorerBaseUrl}/tx/${data}`;
    }
    case 'token': {
      return `${blockExplorerBaseUrl}/token/${data}`;
    }
    case 'block': {
      return `${blockExplorerBaseUrl}/block/${data}`;
    }
    case 'address':
    default: {
      return `${blockExplorerBaseUrl}/address/${data}`;
    }
  }
}

// returns the checksummed address if the address is valid, otherwise returns false
export function isAddress(value: any): string | false {
  try {
    return getAddress(value);
  } catch {
    return false;
  }
}

// shorten the checksummed version of the input address to have 0x + 4 characters at start and end
export function shortenAddress(address: string, chars = 4): string {
  if (!address) return;
  const parsed = isAddress(address);
  if (!parsed) {
    throw Error(`Invalid 'address' parameter '${address}'.`);
  }
  return `${parsed.substring(0, chars + 2)}...${parsed.substring(42 - chars)}`;
}

export function shortenTxHash(txHash: string, chars = 12): string {
  if (!txHash) return;

  return `${txHash.substring(0, chars + 2)}...${txHash.substring(66 - chars)}`;
}

export function getSigner(library: Web3Provider, account: string): JsonRpcSigner {
  return library.getSigner(account).connectUnchecked();
}

export function getProviderOrSigner(library: Web3Provider, account?: string): Web3Provider | JsonRpcSigner {
  return account ? getSigner(library, account) : library;
}

export function getContract(address: string, ABI: any, library: Web3Provider, account?: string): Contract {
  if (!isAddress(address) || address === AddressZero) {
    throw Error(`Invalid 'address' parameter '${address}'.`);
  }
  return new Contract(address, ABI, getProviderOrSigner(library, account) as any);
}

// add 10%
export function calculateGasMargin(value: BigNumber): BigNumber {
  return value.mul(BigNumber.from(10000).add(BigNumber.from(1000))).div(BigNumber.from(10000));
}

// add 15%
export function calculateGasMargin15(value: BigNumber): BigNumber {
  return value.mul(BigNumber.from(15000).add(BigNumber.from(1500))).div(BigNumber.from(15000));
}

export function bigNumberToStringifiedNumber(value: BigNumber): string {
  return String(value);
}

export function etherToWei(value: string): string {
  if (!value) return;
  return ethers.utils.parseUnits(value, 'ether').toString();
}

export function formatUnits(
  value: string,
  unitType: string | number,
  useGrouping = true,
  maximumFractionDigits = 2,
  minimumFractionDigits = 0,
): string {
  if (!value || !unitType) return;
  return Number(ethers.utils.formatUnits(value, unitType)).toLocaleString('en-US', {
    useGrouping,
    maximumFractionDigits,
    minimumFractionDigits,
  });
}

export function weiToEther(value: string): string {
  if (!value) return;
  return ethers.utils.formatEther(value).toString();
}

export function bytes32ToString(value: string): string {
  if (!value) return;
  return ethers.utils.parseBytes32String(value);
}

export function stringToBytes32(value: string): string {
  if (!value) return;
  return ethers.utils.formatBytes32String(value);
}

export function secondsToMinutes(seconds: number | string): string {
  if (!seconds) return;
  return `${Number(seconds) / 60} Minute(s)`;
}

export function secondsToHours(seconds: number | string): string {
  if (!seconds) return;
  return `${Number(seconds) / 60 / 60} Hour(s)`;
}

export function unixToDate(timestampInSeconds: number | string): string {
  if (!timestampInSeconds) return;
  return new Date(Number(timestampInSeconds) * 1000).toUTCString();
}

export function formattedDate(timestampInSeconds: number | string): string {
  if (!timestampInSeconds) return;
  return moment(new Date(Number(timestampInSeconds) * 1000)).format('DD/MM/YYYY');
}

