import moment from 'moment';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useActiveWeb3React } from '../../hooks';
import { fetchFaucetTokensFromBlockchain } from '../../hooks/Tokens';
import { useFaucetContract, useWeb3 } from '../../hooks/useContract';
import { AppDispatch } from '../index';
import { addFaucetToken } from './actions';

export default function Updater() {
  const dispatch = useDispatch<AppDispatch>();

  const { chainId } = useActiveWeb3React();
  const faucetContract = useFaucetContract();
  const web3 = useWeb3()();

  const currentTimestampInSec = Math.floor(moment.now() / 1000);

  useEffect(() => {
    if (!chainId || !faucetContract || !web3 || !currentTimestampInSec) return null;
    fetchFaucetTokensFromBlockchain(faucetContract, web3)
      .then(async tokens => {
        for (let token of tokens) {
          const faucetAllowance = await faucetContract.faucetAmount(token.address);
          const waitTime = String(await faucetContract.waitTime());

          dispatch(
            addFaucetToken({
              chainId,
              timestamp: currentTimestampInSec,
              tokenDetails: {
                address: token.address,
                decimals: token.decimals,
                name: token.name,
                symbol: token.symbol,
                allowance: String(faucetAllowance),
                waitTime,
              },
            }),
          );
        }
      })
      .catch(error => {
        console.log(`Failed to retrieve supported tokens. Reason: ${error.message}`);
      });
  }, [chainId, dispatch]);

  return null;
}
