export interface SubscriptionDetails {
  id: BigNumber;
  billingModelID: BigNumber;
  name: string;
  bmName: string;
  merchantName: string;
  uniqueReference: string;
  merchantURL: string;
  subscriber: string;
  paymentAmount: BigNumber;
  amount: BigNumber;
  settlementToken: string;
  frequency: string;
  paymentToken: string;
  numberOfPayments?: BigNumber;
  remainingPayments?: BigNumber;
  startTimestamp: BigNumber;
  cancelTimestamp: BigNumber;
  cancelledBy?: string;
  nextPaymentTimestamp: BigNumber;
  lastPaymentTimestamp: BigNumber;
  pullPaymentIDs: BigNumber[];
  initialAmount: BigNumber;
  trialPeriod: BigNumber;
  totalPayments: BigNumber;
}

export interface SerializedSubscriptionDetails {
  id: string;
  subscriber: string;
  amount: string;
  pullPaymentIDs: string[];
  pullPayments: SerializedPullPayment[];
  billingModelId?: string;
  billingModel?: SerializedBillingModel;
  billingModelType?: BillingModelTypes;
  settlementToken?: string;
  paymentToken?: string;
  numberOfPayments?: string;
  startTimestamp?: string;
  cancelTimestamp?: string;
  cancelledBy?: string;
  nextPaymentTimestamp?: string;
  lastPaymentTimestamp?: string;
  initialAmount?: string;
  trialPeriod?: string;
  name?: string;
  frequency?: string;
  uniqueReference: string;
  totalPayments?: string;
}

export function serializeSubscription(
  subID: string,
  sub: SubscriptionDetails,
  initialAmount?: BigNumber,
  trialPeriod?: BigNumber,
): SerializedSubscriptionDetails {
  const pullPaymentIDs = [];
  sub?.pullPaymentIDs?.forEach(id => {
    pullPaymentIDs.push(bigNumberToStringifiedNumber(id));
  });
  return {
    id: subID,
    billingModelId: sub.billingModelID ? bigNumberToStringifiedNumber(sub.billingModelID) : null,
    subscriber: sub.subscriber,
    name: sub?.name ? sub?.name : sub?.bmName ? sub?.bmName : null,
    amount: sub.amount
      ? bigNumberToStringifiedNumber(sub.amount)
      : sub.paymentAmount
      ? bigNumberToStringifiedNumber(sub.paymentAmount)
      : null,
    settlementToken: sub.settlementToken ? sub.settlementToken : null,
    paymentToken: sub.paymentToken ? sub.paymentToken : null,
    frequency: sub.frequency ? sub.frequency : null,
    numberOfPayments: sub.numberOfPayments
      ? bigNumberToStringifiedNumber(sub.numberOfPayments)
      : sub?.remainingPayments
      ? bigNumberToStringifiedNumber(sub?.remainingPayments)
      : null,
    startTimestamp: sub.startTimestamp ? bigNumberToStringifiedNumber(sub.startTimestamp) : null,
    cancelledBy: sub.cancelledBy || null,
    cancelTimestamp: sub.cancelTimestamp ? bigNumberToStringifiedNumber(sub.cancelTimestamp) : null,
    nextPaymentTimestamp: sub.nextPaymentTimestamp ? bigNumberToStringifiedNumber(sub.nextPaymentTimestamp) : null,
    lastPaymentTimestamp: sub.lastPaymentTimestamp ? bigNumberToStringifiedNumber(sub.lastPaymentTimestamp) : null,
    pullPaymentIDs: pullPaymentIDs,
    uniqueReference: sub.uniqueReference,
    initialAmount: initialAmount ? bigNumberToStringifiedNumber(initialAmount) : null,
    trialPeriod: trialPeriod ? bigNumberToStringifiedNumber(trialPeriod) : null,
    totalPayments: sub?.totalPayments ? bigNumberToStringifiedNumber(sub?.totalPayments) : null,
  } as SerializedSubscriptionDetails;
}

