import moment from 'moment';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useActiveWeb3React } from '../../hooks';
import { fetchSupportedTokenFromBlockchain } from '../../hooks/Tokens';
import { useCoreRegistryContract, useWeb3 } from '../../hooks/useContract';
import { AppDispatch } from '../index';
import { addSupportedToken } from './actions';

export default function Updater() {
  const dispatch = useDispatch<AppDispatch>();

  const { chainId } = useActiveWeb3React();
  const coreRegistryContract = useCoreRegistryContract();
  const web3 = useWeb3()();

  const currentTimestampInSec = Math.floor(moment.now() / 1000);

  useEffect(() => {
    if (!chainId || !coreRegistryContract || !web3 || !currentTimestampInSec) return null;
    fetchSupportedTokenFromBlockchain(coreRegistryContract, web3)
      .then(tokens => {
        for (let token of tokens) {
          dispatch(
            addSupportedToken({
              chainId,
              timestamp: currentTimestampInSec,
              tokenDetails: {
                address: token.address,
                decimals: token.decimals,
                name: token.name,
                symbol: token.symbol,
              },
            }),
          );
        }
      })
      .catch(error => {
        console.log(`Failed to retrieve supported tokens. Reason: ${error.message}`);
      });
  }, [chainId, dispatch]);

  return null;
}
