import { createAction } from '@reduxjs/toolkit';
import { ChainId } from '../../constants';
import { SerializedSubscriptionDetails } from '../../utils/blockchain-serializers';
import { BillingModelTypes } from '../../utils';

export const clearAllMySubscriptions = createAction<{ chainId: ChainId }>('my_subscriptions/clearAllMySubscriptions');

export const addMySubscription = createAction<{
  chainId: ChainId;
  billingModelType: BillingModelTypes;
  subscriptionDetails: SerializedSubscriptionDetails;
}>('my_subscriptions/addMySubscription');
