import { createAction } from '@reduxjs/toolkit';
import { ChainId } from '../../constants';
import { BillingModelTypes } from '../../utils';
import { IPullPaymentDetails } from './hooks';

export const clearAllPullPaymentTxs = createAction<{ chainId: ChainId }>('pull_payment_txs/clearAllPullPaymentTxs');

export const addPullPaymentTx = createAction<{
  chainId: ChainId;
  billingModelType: BillingModelTypes;
  billingModelId: string;
  bmSubscriptionId: string;
  pullPaymentTx: IPullPaymentDetails;
}>('pull_payment_txs/addPullPaymentTx');
