import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { useCallback, useMemo } from 'react';
import { Token } from 'quickswap-sdk';

import { AppDispatch, AppState } from '../index';
import { SerializedToken, toggleURLWarning, updateUserDarkMode } from './actions';
import { useActiveWeb3React } from '../../hooks';
import { ChainId } from '../../constants';

export function useIsUserOnlyMerchant(): boolean | null {
  return useSelector((state: AppState) => {
    if (state.user.mySubscriptionsCount === null || state.user.myBillingModelsCount === null) {
      return null;
    }

    // A user is only merchant when he has billing models
    // but does NOT have any subscriptions
    return state.user.mySubscriptionsCount === 0 && state.user.myBillingModelsCount > 0;
  });
}

export function useMyBillingModelsCount(): number {
  return useSelector((state: AppState) => state.user.myBillingModelsCount);
}

export function useMySubscriptionsCount(): number {
  return useSelector((state: AppState) => state.user.mySubscriptionsCount);
}

export function useIsDarkMode(): boolean {
  const { userDarkMode, matchesDarkMode } = useSelector<
    AppState,
    { userDarkMode: boolean | null; matchesDarkMode: boolean }
  >(
    ({ user: { matchesDarkMode, userDarkMode } }) => ({
      userDarkMode,
      matchesDarkMode,
    }),
    shallowEqual,
  );

  return userDarkMode === null ? matchesDarkMode : userDarkMode;
}

export function useURLWarningVisible(): boolean {
  return useSelector((state: AppState) => state.user.URLWarningVisible);
}

export function useURLWarningToggle(): () => void {
  const dispatch = useDispatch();
  return useCallback(() => dispatch(toggleURLWarning()), [dispatch]);
}

export function useDarkModeManager(): [boolean, () => void] {
  const dispatch = useDispatch<AppDispatch>();
  const darkMode = useIsDarkMode();

  const toggleSetDarkMode = useCallback(() => {
    dispatch(updateUserDarkMode({ userDarkMode: !darkMode }));
  }, [darkMode, dispatch]);

  return [darkMode, toggleSetDarkMode];
}

function deserializeToken(serializedToken: SerializedToken): Token {
  return new Token(
    serializedToken.chainId,
    serializedToken.address,
    serializedToken.decimals,
    serializedToken.symbol,
    serializedToken.name,
  );
}

export function useUserAddedTokens(): Token[] {
  const { chainId } = useActiveWeb3React();
  const serializedTokensMap = useSelector<AppState, AppState['user']['tokens']>(({ user: { tokens } }) => tokens);

  return useMemo(() => {
    if (!chainId || !serializedTokensMap) return [];
    // console.log(serializedTokensMap);
    return Object.values(serializedTokensMap[chainId as ChainId] ?? {}).map(deserializeToken);
  }, [serializedTokensMap, chainId]);
}
