import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import AppBody from '../../pages/AppBody';
import { TYPE } from '../../theme';
import { AutoColumn } from '../Column';
import { Text } from 'rebass';
import { ButtonPrimary } from '../Button';
import { Box } from 'rebass/styled-components';
import { useHistory } from 'react-router-dom';
import { useWalletModalToggle } from '../../state/application/hooks';
import { InfoCard } from '../InfoCard';
import { NavToggle } from '../../pages/App';

const MainContent = styled.div`
  &.open {
    padding-left: 220px;
    padding-right: 2rem;
    margin-top: 1rem;
  }

  display: flex;
  width: 100%;
  flex-direction: column;
  padding-left: 100px;
  padding-right: 20px;
`;
const ManageDiv = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 0;
`;
const ManageButton = styled.div`
  margin: auto;

  #bm-connect-to-wallet {
    padding: 16px;
    border-radius: 9px;
  }

  #create-bm-button {
    padding: 16px;
    width: 180px;
    background: #7023ff;
    margin: 0 auto;
    border-radius: 9px;
  }
`;
const NoMarginLeft = styled.div`
  margin-left: 0;
`;

export default function ConnectWallet() {
  const { t } = useTranslation();
  const selectNavToggle = useContext(NavToggle);
  const toggleWalletModal = useWalletModalToggle();
  const history = useHistory();

  return (
    <>
      <MainContent className={selectNavToggle ? ' open' : ''}>
        <NoMarginLeft>
          <AutoColumn style={{ width: '55%' }} gap="lg" justify="center">
            <InfoCard title={t('billingModel')} info={t('setModelDescriptionConnectWallet')} />
          </AutoColumn>
        </NoMarginLeft>
        <AppBody>
          <ManageDiv>
            <AutoColumn gap="lg" justify="center">
              <TYPE.body color="#959595" textAlign="center" marginBottom="20px" fontSize="13px">
                {t('connectionInfo')}
              </TYPE.body>
            </AutoColumn>

            <Box
              as="form"
              onSubmit={e => {
                e.preventDefault();
                history.push('/connect');
              }}
            >
              <ManageButton>
                <ButtonPrimary id="bm-connect-to-wallet" onClick={toggleWalletModal}>
                  <Text fontWeight={400} fontSize={16}>
                    {t('connectWallet')}
                  </Text>
                </ButtonPrimary>
              </ManageButton>
            </Box>
          </ManageDiv>
        </AppBody>
      </MainContent>
    </>
  );
}
