import { createAction } from '@reduxjs/toolkit';

export interface SerializedToken {
  chainId: number;
  address: string;
  decimals: number;
  symbol?: string;
  name?: string;
}

export const updateMatchesDarkMode = createAction<{ matchesDarkMode: boolean }>('user/updateMatchesDarkMode');
export const updateUserDarkMode = createAction<{ userDarkMode: boolean }>('user/updateUserDarkMode');
export const updateUserExpertMode = createAction<{ userExpertMode: boolean }>('user/updateUserExpertMode');
export const toggleURLWarning = createAction<void>('app/toggleURLWarning');

export const updateMyBillingModelsCount = createAction<{
  myBillingModelsCount: number;
}>('user/updateMyBillingModelsCount');
export const updateMySubscriptionsCount = createAction<{
  mySubscriptionsCount: number;
}>('user/updateMySubscriptionsCount');

export const addSerializedToken = createAction<{
  serializedToken: SerializedToken;
}>('user/addSerializedToken');
export const removeSerializedToken = createAction<{
  chainId: number;
  address: string;
}>('user/removeSerializedToken');
