import styled from 'styled-components';
import React, { createContext, Suspense, useState } from 'react';
import { Route, Switch } from 'react-router-dom';
import DarkModeQueryParamReader from '../theme/DarkModeQueryParamReader';
import GoogleAnalyticsReporter from '../components/analytics/GoogleAnalyticsReporter';
import URLWarning from '../components/Header/URLWarning';
import Header from '../components/Header';
import Popups from '../components/Popups';
import Web3ReactManager from '../components/Web3ReactManager';
import ConnectWallet from '../components/ConnectWallet';
import NavBar from './SideBarDesign/NavBar';
import PrivateRoute from '../routes/PrivateRoutes';
import ProtectedRoutes from '../routes/ProtectedRoutes';
import PublicRoute from '../routes/PublicRoutes';
import { useWeb3React } from '@web3-react/core';
import BreadCrumb from './BreadCrumb';

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
  .kGowO {
    background-color: #fff !important;
    color: #000;
    border: none;
  }
  h3 {
    color: #000;
  }
  .iqTDpN {
    background: #edeef2;
    color: #000;
  }
`;
const HeaderWrapper = styled.div`
  ${({ theme }) => theme.flexRowNoWrap}
  width: 100%;
  justify-content: space-between;
`;
const BodyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  background: #fff;
  align-items: center;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    padding: 16px;
    padding-top: 2rem;
  `};
  z-index: 1;
`;

const NavToggle = createContext(true);

export default function App() {
  const [isMenuOpen, setIsMenuOpen] = useState(true);

  const { active } = useWeb3React();
  const isAuthenticated = active;

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <NavToggle.Provider value={isMenuOpen}>
      <Suspense fallback={null}>
        <Route component={GoogleAnalyticsReporter} />
        <Route component={DarkModeQueryParamReader} />
        <AppWrapper className="Main-Wrap">
          <URLWarning />
          <HeaderWrapper>
            <Header onMenuToggle={toggleMenu} />
          </HeaderWrapper>
          <BodyWrapper>
            <NavBar isMenuOpen={isMenuOpen} onMenuToggle={toggleMenu} />
            <Popups />
            <BreadCrumb isMenuOpen={isMenuOpen} onMenuToggle={toggleMenu} />
            <Web3ReactManager>
              <Switch>
                <PublicRoute path="/connect" isAuthenticated={isAuthenticated}>
                  <Route path="/connect" component={ConnectWallet} />
                </PublicRoute>
                <PrivateRoute path="/" isAuthenticated={isAuthenticated}>
                  <ProtectedRoutes />
                </PrivateRoute>
              </Switch>
            </Web3ReactManager>
          </BodyWrapper>
        </AppWrapper>
      </Suspense>
    </NavToggle.Provider>
  );
}
export { NavToggle };
