import { createReducer } from '@reduxjs/toolkit';
import { clearAllPullPaymentTxs, addPullPaymentTx } from './actions';

export interface PullPaymentTxsState {
  [chainId: number]: {
    [uniqueId: string]: {
      [txHash: string]: any;
    };
  };
}

export const initialState: PullPaymentTxsState = {};

export default createReducer(initialState, builder =>
  builder
    .addCase(clearAllPullPaymentTxs, (pullPaymentTxs, { payload: { chainId } }) => {
      if (!pullPaymentTxs[chainId]) return;
      pullPaymentTxs[chainId] = {};
    })
    .addCase(
      addPullPaymentTx,
      (pullPaymentTxs, { payload: { chainId, billingModelType, billingModelId, bmSubscriptionId, pullPaymentTx } }) => {
        if (
          pullPaymentTxs[chainId]?.[`${billingModelType}_${billingModelId}_${bmSubscriptionId}`]?.[
            pullPaymentTx.transactionHash
          ]
        ) {
          throw Error('Attempted to add existing transaction.');
        }

        const pullPaymentTxsPerChain = pullPaymentTxs[chainId] ?? {};

        const txs = pullPaymentTxsPerChain[`${billingModelType}_${billingModelId}_${bmSubscriptionId}`] ?? {};

        txs[`${pullPaymentTx.transactionHash}`] = pullPaymentTx;

        pullPaymentTxsPerChain[`${billingModelType}_${billingModelId}_${bmSubscriptionId}`] = txs;
        pullPaymentTxs[chainId] = pullPaymentTxsPerChain;
      },
    ),
);
