import { createAction } from '@reduxjs/toolkit';
import { ChainId } from '../../constants';
import { SupportedTokenDetails } from './reducer';

export const clearAllSupportedTokens = createAction<{ chainId: ChainId }>('supported_tokens/clearAllSupportedTokens');
export const addSupportedToken = createAction<{
  chainId: ChainId;
  timestamp: number;
  tokenDetails: SupportedTokenDetails;
}>('supported_tokens/addSupportedToken');
