import { createReducer } from '@reduxjs/toolkit';
import { addSupportedToken, clearAllSupportedTokens } from './actions';

export interface SupportedTokenDetails {
  address: string;
  decimals: number;
  name: string;
  symbol: string;
}

export interface SupportedTokenState {
  [chainId: number]: {
    [tokenAddress: string]: SupportedTokenDetails;
  };
}

export const initialState: SupportedTokenState = {};

export default createReducer(initialState, builder =>
  builder
    .addCase(clearAllSupportedTokens, (supportedTokens, { payload: { chainId } }) => {
      if (!supportedTokens[chainId]) return;
      supportedTokens[chainId] = {};
    })
    .addCase(addSupportedToken, (supportedTokens, { payload: { chainId, timestamp, tokenDetails } }) => {
      if (supportedTokens[chainId]?.[tokenDetails.address]) return;
      const tokens = supportedTokens[chainId] ?? {};
      tokens[tokenDetails.address] = tokenDetails;
      supportedTokens[chainId] = tokens;
    }),
);
