import { createAction } from '@reduxjs/toolkit';
import { ChainId } from '../../constants';
import { FaucetTokenDetails } from './reducer';

export const clearAllFaucetTokens = createAction<{ chainId: ChainId }>('faucet_tokens/clearAllFaucetTokens');
export const addFaucetToken = createAction<{
  chainId: ChainId;
  timestamp: number;
  tokenDetails: FaucetTokenDetails;
}>('faucet_tokens/addFaucetToken');
